import React, { Component } from "react";
import "./MessageList.less";

import MessageListBody from "./MessageListPart/MessageListBody";

class Get extends Component {
  constructor() {
    super();
    this.state = {
      popFloorShow: false,
      scrollStop: false,
      messageData: [
        {
          pho: "http://www.cdhdky.com/images/ttt.jpg",
          name: "",
          message: "",
          redBag: { self: false }
        },
        {
          pho: "http://www.cdhdky.com/images/ttt.jpg",
          name: "",
          message: "",
          redBag: {
            receiveRedBag: "",
            self: false,
            tips: "1",
            total: "30",
            sendTime: "1508614531210",
            redBag: [
              { name: "", money: "1", rest: "30" },
              { name: "k", money: "2", rest: "30" },
              { name: "", money: "4", rest: "30" },
              { name: "", money: "4", rest: "30" },
              { name: "", money: "4", rest: "30" }
            ]
          }
        },
        {
          pho: "http://www.cdhdky.com/images/ttt.jpg",
          name: "",
          message: "",
          redBag: { self: false }
        },
        {
          pho: "http://www.cdhdky.com/images/ttt.jpg",
          name: "",
          message: "",
          redBag: {
            receiveRedBag: "1",
            self: false,
            tips: "",
            total: "30",
            sendTime: "1538614531210",
            redBag: [
              { name: "", money: "1", rest: "30" },
              { name: "k", money: "2", rest: "30" },
              { name: "", money: "4", rest: "30" },
              { name: "", money: "4", rest: "30" },
              { name: "", money: "4", rest: "30" }
            ]
          }
        },
        {
          pho: "http://www.cdhdky.com/images/ttt.jpg",
          name: "",
          message: "",
          redBag: { self: false }
        },
        {
          pho: "http://www.cdhdky.com/images/ttt.jpg",
          name: "",
          message: "",
          redBag: {
            receiveRedBag: "1",
            self: false,
            tips: "",
            total: "30",
            sendTime: "1538614531210",
            redBag: [
              { name: "", money: "1", rest: "30" },
              { name: "k", money: "2", rest: "30" },
              { name: "", money: "4", rest: "30" },
              { name: "", money: "4", rest: "30" },
              { name: "", money: "4", rest: "30" }
            ]
          }
        },
        {
          pho: "http://www.cdhdky.com/images/ttt.jpg",
          name: "",
          message: "",
          redBag: { self: false }
        },
        {
          pho: "http://www.cdhdky.com/images/ttt.jpg",
          name: "",
          message: "",
          redBag: {
            receiveRedBag: "2",
            self: false,
            tips: "",
            total: "30",
            sendTime: "1538614531210",
            redBag: [
              { name: "", money: "1", rest: "30" },
              { name: "k", money: "2", rest: "30" },
              { name: "", money: "4", rest: "30" },
              { name: "", money: "4", rest: "30" },
              { name: "", money: "4", rest: "30" }
            ]
          }
        }
      ]
    };
  }
  getPopFloorShow(val) {
    this.setState(val);
  }
  getSubmitData(val) {
    let message = {
      pho: "http://www.cdhdky.com/images/ttt.jpg",
      name: "",
      message: "",
      redBag: {
        receiveRedBag: "2",
        self: true,
        tips: "",
        total: "30",
        sendTime: "1538614531210",
        redBag: [
          { name: "", money: "1", rest: "30" },
          { name: "k", money: "2", rest: "30" },
          { name: "", money: "4", rest: "30" },
          { name: "", money: "4", rest: "30" },
          { name: "", money: "4", rest: "30" }
        ]
      }
    };
    let old = this.state.messageData;
    old.push(message);
    this.setState({
      messageData: old
    });
  }
  getScrollStop(val) {
    this.setState(val);
  }
  render() {
    return (
      <div className={this.state.scrollStop ? "get stop-scroll" : "get"}>
        <MessageListBody
          messageData={this.state.messageData}
          sendScrollStop={val => this.getScrollStop(val)}
        />
      </div>
    );
  }
}
export default Get;



