import React, { Component } from "react";
import Header from "../../../../base-page/Header";
import Search from "../../../../base-page/Search";
import "./index.less";

class Collection extends Component {
  constructor() {
    super();
    this.state = {
      application: [
        { title: "", route: "" },
        { title: "", route: "" },
        { title: "", route: "" },
        { title: "", route: "" },
        { title: "", route: "" },
        { title: "", route: "" },
        { title: "", route: "" },
        { title: "", route: "" }
      ]
    };
  }
  handleClick = () => {
    window.history.go(0);
  };
  render() {
    return (
      <div component={"me-collection-component"}>
        <Header>
          <li onClick={this.handleClick}></li>
          <li></li>
          <li>+</li>
        </Header>
        <div className={"me-collection"}>
          <Search />
          <div className={"top"}>
            <ul>
              {this.state.application.map((item, index) => (
                <li key={index}>{item.title}</li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    );
  }
}
export default Collection;



