import React, { Component } from "react";
import Header from "../../base-page/Header";
import Search from "../../base-page/Search";
import { Link } from "react-router-dom";
import Mould from "./Mould";
import Footer from "../../base-page/Footer";

import "./Message.less";

class Message extends Component {
  constructor() {
    super();
    this.state = {
      mouldShow: false,
      message: [
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: " ",
          tips: "",
          time: "5:44",
          route: "Qun"
        },
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: "",
          tips: "1",
          time: "5:44",
          route: "dingyue"
        },
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: "Team",
          tips: "",
          time: "5:44",
          route: "Qun"
        },
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: "",
          tips: "",
          time: "5:44",
          route: "server"
        },
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: "~&CBG ",
          tips: "",
          time: "5:44",
          route: "Qun"
        },
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: "",
          tips: "",
          time: "5:44",
          route: "healp"
        },
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: "",
          tips: "",
          time: "5:44",
          route: "yinhang"
        },
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: "",
          tips: "",
          time: "5:44",
          route: "yundong"
        },
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: "",
          tips: "",
          time: "5:44",
          route: "Qun"
        },
        {
          ico: "http://www.cdhdky.com/images/ttt.jpg",
          title: "",
          tips: "",
          time: "5:44",
          route: ""
        }
      ]
    };
  }
  handleClick = () => {
    this.setState({
      mouldShow: false
    });
  };
  handleClick = () => {
    this.setState({ mouldShow: true });
  };
  render() {
    return (
      <div>
        {!this.state.mouldShow && (
          <div className={"message"}>
            <Header>
              <li />
              <li></li>
              <li className={"more"} />
            </Header>
            <Search />
            <div className={"content"}>
              <ul>
                {this.state.message.map((item, index) => (
                  <li key={index} onClick={this.handleClick}>
                    <Link to={"/Message/" + item.route}>
                      <div className={"left"}>
                        <img src={item.ico} alt="" />
                        <div>
                          <p>{item.title}</p>
                          <p>{item.tips}</p>
                        </div>
                      </div>
                      <div className={"right"}>{item.time}</div>
                    </Link>
                  </li>
                ))}
              </ul>
            </div>
            <Footer />
          </div>
        )}
        {this.state.mouldShow && <Mould />}
      </div>
    );
  }
}
export default Message;



