import React, { Component } from "react";

class RedBagReceive extends Component {
  render() {
    return (
      <li>
        <span>{"" + this.props.name}</span>
        <span>{"" + this.props.val + ""}</span>
        <span>{this.props.rest || 0}</span>
      </li>
    );
  }
}
export default RedBagReceive;



